Dropzone.autoDiscover = false;
Dropzone.prototype.defaultOptions.dictRemoveFile = 'Удалить';

$(function() {
  const files = {};
  $('#fileUpload').dropzone({
    url: 'send.php',
    addRemoveLinks: true,
    maxFiles: 2,
    maxFilesize: 50, // MB
    success: function(file, response) {
      var imgName = response;
      file.previewElement.classList.add('dz-success');
    },
    error: function(file, response) {
      console.log(file.size)
      if (file.size > 50000000) {
        $(file.previewElement).remove();
        Swal.fire(
          'Ошибка',
          'Вы не можете добавить файл размером больше 50Мб!',
          'error'
        );
        return false;
      } else {
        if (file.size > 50000000) {
          $(file.previewElement).remove();
          Swal.fire('Ошибка', 'Ошибка при загрузке файла!', 'error');
          return false;
        }
      }
    },
    accept: function(file) {
      if ($('.dz-preview').length > 2) {
        $(file.previewElement).remove();
        Swal.fire(
          'Ошибка',
          'Вы не можете добавить больше двух файлов!',
          'error'
        );
        return false;
      }
      let fileReader = new FileReader();
      files[$('.dz-preview').length] = file;
      // console.log(files);
      fileReader.readAsDataURL(file);
      fileReader.onloadend = function() {
        // console.log(fileReader);
        let content = fileReader.result;
        $('#file').val(content);
        $('input[name=file_size]').val(file.size);
        $('input[name=file_name]').val(file.name);
        $('input[name=file_path]').val(file.pathname);
        file.previewElement.classList.add('dz-success');
      };
      file.previewElement.classList.add('dz-complete');
      $('.get__form').validate();
    }
  });

  $('.get__form').validate({
    rules: {
      phone: {
        required: true,
        phone: true
      },
      email: {
        required: true,
        email: true
      }
    },

    onkeyup: function(element, event) {
      $(element).valid(); // check the current field on valid to prevent on blur check
      let form = $(element).closest('form'); // parent form
      let formInputs = form.find('.default-input');
      let valid = 0; // declare valid fields counter
      formInputs.each(function() {
        // loop throw all form fields and increment valid if it's valid
        if ($(this).hasClass('valid')) {
          valid++;
        }
      });

      if (formInputs.length === valid ) {
        // check the total valid field with the current valid fields
        form.find('button').removeClass('button--disabled');
        form.find('button').attr('disabled', false);
      } else {
        form.find('button').addClass('button--disabled');
        form.find('button').attr('disabled', true);
      }
    }
  });

  $('.get__form').submit(function(e) {
    // const formData = $(this).serializeArray();
    var data = new FormData();
    $.each( files, function( key, value ){
      data.append( key, value );
    });
    data.append('phone', $('.get__form').find('input[name=phone]').val())
    data.append('email', $('.get__form').find('input[name=email]').val())
    data.append( 'my_file_upload', 1 );
    e.preventDefault();
    $.ajax({
      method: "POST",
      url: "send_file.php",
      data: data,
      processData : false,
      contentType : false, 
      success: function() {
        console.log(data);
        setTimeout(() => {
          window.location.href = window.location.origin + checkPathName('index.php') + 'thx.html';
        }, 500);
      }
    });
  });
});
