jQuery.validator.setDefaults({
  debug: true,
  success: 'valid'
});



jQuery.validator.addMethod(
  'phone',
  function(value, element) {
    return value.match(/\d/g).join('').length === 11;
  },
  'Введите правильный номер телефона'
);

$('input[name=phone]').inputmask('+7(999) 999-99-99', { autoclear: false });

$(function() {
  new WOW().init();

  $('[data-fancybox]').fancybox({});

  ymaps.ready(init);

  function init() {
    var myMap = new ymaps.Map(
      'map',
      {
        center: [56.837126, 60.656633],
        zoom: 17,
        controls: []
      },
      {
        controls: []
      }
    );

    myMap.geoObjects
      .add(
        new ymaps.Placemark(
          [56.836706, 60.654777],
          {
            balloonContent:
              'улица Коминтерна, 16'
          },
          {
            preset: 'islands#icon',
            iconColor: '#0095b6'
          }
        )
      );
      $(window).on('resize load', debounce(function() {
        if ($(window).width() + getScrollbarWidth() > 769) {
          myMap.setCenter([56.837126, 60.656633], 17);
        } else {
          myMap.setCenter([56.836706, 60.654777], 17);
        }
      }, 50))
  }

});

function debounce(func, wait, immediate) {
  var timeout;
  return function () {
    var context = this,
      args = arguments;
    var later = function () {
      timeout = null;
      if (!immediate) func.apply(context, args);
    };
    var callNow = immediate && !timeout;
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
    if (callNow) func.apply(context, args);
  };
}

function getScrollbarWidth() {
  var outer = document.createElement('div');
  outer.style.visibility = 'hidden';
  outer.style.width = '100px';
  outer.style.msOverflowStyle = 'scrollbar';

  document.body.appendChild(outer);

  var widthNoScroll = outer.offsetWidth;
  // force scrollbars
  outer.style.overflow = 'scroll';

  // add innerdiv
  var inner = document.createElement('div');
  inner.style.width = '100%';
  outer.appendChild(inner);

  var widthWithScroll = inner.offsetWidth;

  // remove divs
  outer.parentNode.removeChild(outer);
  return widthNoScroll - widthWithScroll;
}


var mac = navigator.platform.match(/(Mac|iPhone|iPod|iPad)/i) ? true : false;

if (mac) {
    document.documentElement.classList.add("apple");
}

function hasScrollbar() {
  return $(document).height() > $(window).height();
}

function modalOpenMac() {
    // console.log("open", mac);
    if (mac) {
        if ($(document).height() > $(window).height() && !$("html").hasClass("apple-fix")) {
            var scrollTop = $("html").scrollTop() ? $("html").scrollTop() : $("body").scrollTop(); // Works for Chrome, Firefox, IE...
            $("html").css("top", -scrollTop);
            document.documentElement.classList.add("apple-fix");
        }
    }
}

function modalCloseMac() {
    // console.log("close", mac);
    if (mac) {
        var scrollTop = parseInt($("html").css("top"));
        document.documentElement.classList.remove("apple-fix");
        $("html,body").scrollTop(-scrollTop);
    }
}

function checkPathName(href) {
  let pathname = '';
  if(window.location.pathname.indexOf(href) !== -1) {
    pathname = window.location.pathname.substr(0, window.location.pathname.indexOf(href));
  } else {
    pathname = window.location.pathname;
  }
  return pathname
}

if(window.location.pathname.indexOf('thx.html') !== -1) {
  setTimeout(() => {
    window.location.href = window.location.origin + checkPathName('thx.html') + 'index.php';
  }, 10000);
}