$(function() {
  $('.q-block__next').on('click', function(e) {
    e.preventDefault();
    const parrent = $(this).closest('.q-block__question');
    $(parrent).hide();
    $(parrent).next('.q-block__question').fadeIn();
  })

  $('.q-block__form').validate({
    rules: {
      email: {
        required: true,
        email: true
      },
      phone: {
        required: true,
        phone: true
      }
    },

    onkeyup: function(element, event) {
      $(element).valid(); // check the current field on valid to prevent on blur check
      let form = $(element).closest('form'); // parent form
      let formInputs = form.find('.default-input');
      let valid = 0; // declare valid fields counter
      formInputs.each(function() {
        // loop throw all form fields and increment valid if it's valid
        if ($(this).hasClass('valid')) {
          valid++;
        }
      });

      if (formInputs.length === valid ) {
        // check the total valid field with the current valid fields
        form.find('button').removeClass('button--disabled');
        form.find('button').attr('disabled', false);
      } else {
        form.find('button').addClass('button--disabled');
        form.find('button').attr('disabled', true);
      }
    }
  });

  $('.q-block__form').submit(function(e) {
    const formData = $(this).serializeArray();
    console.log(formData);
    e.preventDefault();
    $.ajax({
      method: "POST",
      url: "send.php",
      data: formData,
      success: function() {
        console.log(formData);
        setTimeout(() => {
          window.location.href = window.location.origin + checkPathName('index.php') + 'thx.html';
        }, 500);
      }
    });
  });
});
