$(function () {
  var prevModal;
  $(document).on("click", "[data-modal]", function (e) {
      e.preventDefault();
      var newModal = $(this).data("modal");
      var catalog = $(this).closest('.catalog__item-hover').find('.catalog__item-name').text();
      var fileName = $(this).closest('.catalog__item').data('name');
      if ($(".default-modal.active").length) {
          modalClose();
          setTimeout(function () {
              showModal(newModal);
          }, 300);
      } else {
          showModal(newModal);
          $("input[name=catalog_name]").val(catalog);
          $("input[name=f_name]").val(fileName);
      }
  });
  $(".default-modal__close, [data-modal-close]").click(function () {
      modalClose();
  });

  $(".default-modal").click(function (e) {
      var thisModal = $(this);
      if (thisModal.hasClass("half") && !$(e.target).closest(".default-modal__wrap").length) {
          thisModal.removeClass("active half");
          setTimeout(function () {
              prevModal.addClass("active");
          }, 300);
      } else {
          if (!$(e.target).closest(".default-modal__content").length) {
              callbackClose();
              e.stopPropagation(); //?
          } else {
              // console.log("modal-content click");
          }
      }
  });

  document.addEventListener("keydown", function (e) {
      if (e.key === "Escape" || e.key === "Esc" || e.keyCode === 27) {
          var thisModal = $(".default-modal.half");
          if (thisModal.length) {
              thisModal.removeClass("active half");
              setTimeout(function () {
                  prevModal.addClass("active");
              }, 300);
          } else {
              callbackClose();
          }
      }
  });
});

function showModal(e) {
  $("body").addClass("modal-open");
  var modal = $("." + e + "");
  $("html").addClass("no-scroll");
  // console.log(modal);
  modalOpenMac();
  if (hasScrollbar()) {
      var scrollWidth = getScrollbarWidth();
      $("html").css({
          "margin-right": scrollWidth
      });
      $(".header__wrapper").css({
          "margin-right": scrollWidth
      });
      modal.addClass("active");
  } else {
      modal.addClass("active");
  }

  var isIE = /*@cc_on!@*/ false || !!document.documentMode;
  if (isIE === true) {
      if (modal.find(".default-modal__content").outerHeight() > $(window).height()) {
          modal.css("display", "block");
      } else {
          modal.removeAttr("style");
      }
  }
}

function modalClose() {
  var thisModal = $(this).closest(".default-modal");
  $("input[name=catalog_name]").val('');
  $("input[name=f_name]").val('');
  if (thisModal.hasClass("half")) {
      thisModal.removeClass("active half");
      setTimeout(function () {
          prevModal.addClass("active");
      }, 300);
  } else {
      callbackClose();
  }
}

function callbackClose() {
  $("body").removeClass("modal-open");
  $(".default-modal").removeClass("active");
  setTimeout(function () {
      var scrollTop = parseInt($("html").css("top"));
      $("html").removeClass("no-scroll");
      $("html").css("margin-right", "");
      $(".header__wrapper").css("margin-right", "");
      modalCloseMac();
  }, 300);
}