$(function() {
  $('.catalog__tabs').tabs({
    select: function(event, ui) {
      $(ui.panel).animate({ opacity: 0 });
    },
    show: function(event, ui) {
        $(ui.panel).animate({ opacity: 1.0 }, 1500);
    },
    activate: function() {
      if($('.catalog__grid').hasClass('slick-initialized')) {
        $('.catalog__grid').slick('refresh');
      }
    },
    beforeActivate(event,ui) {
      ui.newPanel.css({'opacity': '0'});
      setTimeout(() => {
        ui.newPanel.animate({'opacity': '1'});
      }, 100);
    }
  });

    const settings_slider = {
      dots: true,
      arrows: false,
      slidesToShow: 1,
      slidesToScroll: 1,
      infinite: false
    };
    $(window).on(
      'load resize orientationchange',
      debounce(function() {
        let $carousel = $('.catalog__grid');
        if ($(window).width() + getScrollbarWidth() > 769) {
          if ($carousel.hasClass('slick-initialized')) {
            $carousel.slick('unslick');
          }
        } else {
          if (!$carousel.hasClass('slick-initialized')) {
            $carousel.slick(settings_slider);
          }
        }
      }, 20)
    );
});
