$(function() {
  const settings_slider = {
    dots: true,
    arrows: false,
    slidesToShow: 2,
    slidesToScroll: 1,
    infinite: false
  };
  $(window).on(
    'load resize orientationchange',
    debounce(function() {
      let $carousel = $('.comments__list');
      if ($(window).width() + getScrollbarWidth() > 600) {
        if ($carousel.hasClass('slick-initialized')) {
          $carousel.slick('unslick');
        }
      } else {
        if (!$carousel.hasClass('slick-initialized')) {
          $carousel.slick(settings_slider);
        }
      }
    }, 20)
  );
})